export default {
  name: 'customItemRender',
  props: {
    type: String,
    config: Object
  },
  render: function(h) {
    switch (this.type) {
      case 'select':
        return selectItem(h, this.config);
      case 'text':
        return inputItem(h, this.config);
      case 'textarea':
        return inputItem(h, this.config);
      case 'date':
        return dateItem(h, this.config);
      case 'time':
        return timeItem(h, this.config);
      case 'inputNumber':
        return inputNumberItem(h, this.config);
      case 'number':
        return inputItem(h, this.config);
      case 'upload':
        return uploadItem(h, this.config);
      case 'cascader':
        return cascaderItem(h, this.config);
      case 'switch':
        return switchItem(h, this.config);
      case 'radioGroup':
        return radioGroupItem(h, this.config);
      case 'checkboxGroup':
        return checkboxGroupItem(h, this.config);
      case 'button':
        return renderButtonItem(h, this.config);
      case 'hidden':
        return inputItem(h, this.config);
      case 'password':
        return inputItem(h, this.config);
      case 'transfer':
        return transferItem(h, this.config);
      default:
        return '';
    }
  }
};

// 下拉框
function selectItem(h, config) {
  const {
    model,
    key,
    props,
    listeners,
    options,
    optionConfig,
    placeholder,
    syncConfig,
    goods,
    value,
    multiple
  } = config;

  let opl = 'label';
  let opv = 'value';
  if (optionConfig) {
    if (optionConfig.label) {
      opl = optionConfig.label;
    }
    if (optionConfig.value) {
      opv = optionConfig.value;
    }
  }

  const opts = options;
  let ops = [];
  if (opts) {
    ops = opts.map((option) => {
      return h('el-option', {
        key: option[opv],
        props: {
          label: option[opl] || option['text'],
          value: option[opv],
          disabled: option.disabled
        }
      });
    });
  }

  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  return h(
    'el-select',
    {
      props: {
        placeholder,
        filterable: true,
        ...pps,
        value: value,
        multiple,
        disabled: goods.readable === 1 || goods.pageDisabled,
        'multiple-limit': goods.itemMultipleLimit
      },
      on: {
        ...on,
        change: (val) => {
          goods.value = val;
          if (model) {
            model[key] = val;
          }
          if (on.change) {
            on.change(val);
          }
        }
      }
    },
    ops
  );
}

// 单文本输入框
function inputItem(h, config) {
  const {
    model,
    key,
    props,
    goods,
    listeners,
    placeholder,
    areaProps,
    syncConfig,
    value
  } = config;
  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  const area = areaProps || {};
  return h('el-input', {
    props: {
      ...pps,
      value: value,
      disabled: goods.readable === 1 || goods.pageDisabled,
      type: goods.formItemType || goods.type,
      clearable: goods.clearAble
    },
    attrs: {
      placeholder: placeholder || pps.placeholder || '请输入',
      ...area
    },
    on: {
      ...on,
      input: (val) => {
        goods.value = val;
        if (model) {
          model[key] = val;
        }
      },
      change: (val) => {
        if (on.change) {
          on.change(val);
        }
      }
    }
  });
}

// 开关
function switchItem(h, config) {
  const {
    model,
    key,
    props,
    goods,
    listeners,
    placeholder,
    areaProps,
    syncConfig,
    value
  } = config;
  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  const area = areaProps || {};
  return h('el-switch', {
    props: {
      ...pps,
      value: value,
      disabled: goods.readable === 1 || goods.pageDisabled,
      type: goods.type
    },
    attrs: {
      placeholder: placeholder || pps.placeholder || '请输入',
      ...area
    },
    on: {
      ...on,
      change: (val) => {
        if (on.change) {
          on.change(val);
        }
        goods.value = val;
        if (model) {
          model[key] = val;
        }
      }
    }
  });
}
// 日期
function dateItem(h, config) {
  const {
    model,
    key,
    props,
    listeners,
    placeholder,
    type,
    syncConfig,
    goods,
    value
  } = config;
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  const on = listeners || {};
  return h('el-date-picker', {
    props: {
      ...pps,
      valueFormat: 'yyyy-MM-dd',
      value: value,
      disabled: goods.readable === 1 || goods.pageDisabled
    },
    attrs: {
      placeholder: placeholder || pps.placeholder || '请输入',
      type: type || 'date'
    },
    on: {
      ...on,
      input: (val) => {
        goods.value = val;
        if (model) {
          model[key] = val;
        }
      },
      change: (val) => {
        if (on.change) {
          on.change(val);
        }
      }
    }
  });
}
