// 上传
function uploadItem(h, config) {
  const { goods, props, btnProps, listeners, iconProps } = config;
  const pps = props || {};
  const btnProp = btnProps || {};
  const iconProp = iconProps || {};
  const on = listeners || {};
  return h(
    'el-upload',
    {
      props: {
        ...pps,
        'file-list': goods.fileList
      },
      on: {
        ...on
      }
    },
    [
      h(
        'el-button',
        {
          props: {
            ...btnProp,
            size: btnProp.size || 'small',
            disabled: goods.readable === 1 || goods.pageDisabled
          }
        },
        [
          h('svg-icon', {
            style: iconProp.styles,
            props: {
              iconClass: iconProp.iconClass
            }
          }),
          goods.btnProp.buttonText || '上传'
        ]
      )
    ]
  );
}

// 级联选择器
function cascaderItem(h, config) {
  const { value, key, props, listeners, options, optionProps } = config;
  // let ops = options();
  const on = listeners || {};
  const pps = props || {};
  return h('el-cascader', {
    props: {
      value: value,
      ...pps,
      options: options,
      props: optionProps
    },
    on: {
      change: (val) => {
        config.value = val;
        if (on.change) {
          on.change(val);
        }
      }
    }
  });
}

// 单选框组
function radioGroupItem(h, config) {
  const { model, key, value, goods, listeners, options } = config;
  const opl = 'label';
  const opv = 'value';
  const opts = options;
  let ops = [];
  if (opts) {
    ops = opts.map((option) => {
      return h(
        'el-radio',
        {
          key: option[opv],
          props: {
            label: option[opv],
            disabled: option.disabled
          }
        },
        option[opl] || option['text']
      );
    });
  }
  const on = listeners || {};
  return h(
    'el-radio-group',
    {
      props: {
        value: value,
        disabled: goods.readable === 1 || goods.pageDisabled
      },
      on: {
        ...on,
        input: (val) => {
          goods.value = val;
          if (model) {
            model[key] = val;
          }
        },
        change: (val) => {
          if (on.change) {
            on.change(val);
          }
        }
      }
    },
    ops
  );
}

// 多选框组
function checkboxGroupItem(h, config) {
  const {
    model,
    key,
    props,
    listeners,
    optionConfig,
    placeholder,
    syncConfig,
    goods,
    value,
    multiple,
    options
  } = config;

  let opl = 'label';
  let opv = 'value';
  if (optionConfig) {
    if (optionConfig.label) {
      opl = optionConfig.label;
    }
    if (optionConfig.value) {
      opv = optionConfig.value;
    }
  }

  const opts = options;
  let ops = [];
  if (opts) {
    ops = opts.map((option) => {
      return h('el-checkbox', {
        key: option[opv],
        props: {
          label: option[opl] || option['text'],
          value: option[opv],
          disabled: option.disabled
        }
      });
    });
  }

  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }

  return h(
    'el-checkbox-group',
    {
      props: {
        placeholder,
        ...pps,
        value: value,
        multiple,
        'collapse-tags': true,
        disabled: goods.readable === 1 || goods.pageDisabled
      },
      on: {
        ...on,
        input: (val) => {
          goods.value = val;
          if (model) {
            model[key] = val;
          }
          if (on.change) {
            on.change(val);
          }
        }
      }
    },
    ops
  );
}

// 按钮
function renderButtonItem(h, config) {
  const { listeners, goods } = config;
  const on = listeners || {};
  return h(
    'el-button',
    {
      props: {
        type: goods.btnProp.buttonType,
        size: goods.btnProp.buttonSize,
        disabled: goods.readable === 1 || goods.pageDisabled
      },
      on: {
        ...on,
        click: (e) => {}
      }
    },
    goods.btnProp.buttonText
  );
}
// 穿梭框
function transferItem(h, config) {
  const {
    model,
    key,
    props,
    listeners,
    options,
    optionConfig,
    placeholder,
    syncConfig,
    goods,
    value,
    multiple
  } = config;

  let opl = 'label';
  let opv = 'value';
  if (optionConfig) {
    if (optionConfig.label) {
      opl = optionConfig.label;
    }
    if (optionConfig.value) {
      opv = optionConfig.value;
    }
  }

  const opts = options;
  const ops = [];
  if (opts) {
    opts.map((option) => {
      ops.push({
        label: option[opl] || option['text'],
        key: option[opv]
      });
    });
  }

  const on = listeners || {};

  return h('el-transfer', {
    props: {
      data: ops,
      filterable: true,
      value: value,
      titles: ['未选中', '已选中'],
      disabled: goods.readable === 1 || goods.pageDisabled
    },
    on: {
      ...on,
      change: (val) => {
        goods.value = val;
        if (model) {
          model[key] = val;
        }
        if (on.change) {
          on.change(val);
        }
      }
    }
  });
}
