// 文件下载
export async function attachmentDownload(path) {
  ajax.download(`/api/file-service/file/export?path=${path}`);
}

// 办理页-表格导出
export async function tableDownload(params, fileName) {
  ajax.download(
    `/api/billow-diplomatic/todo-task/exportExcel`,
    params,
    fileName,
    'post',
    {}
  );
}

// 办理页-表格数据上传
export async function tableUpload(params) {
  return ajax.post(serverPre(`${serverName}/todo-task/importExcel`), params);
}

// 根据任务id查询所有已触发事件监听器
export async function getTaskEventTrigger(taskId) {
  return ajax.get(serverPre(`${serverName}/event/task/event-list/${taskId}`));
}

// 执行事件监听器重试
export async function retryEventTrigger(taskId, eventName, listenerId) {
  return ajax.post(
    serverPre(
      `${serverName}/event/trigger/${taskId}/${eventName}/${listenerId}`
    )
  );
}
